.Playbackdetails-preview {
  width: 100%;
  display: block;
  background: #1E1D23;
  min-width: 1366px;
  min-height: 100vh;
  overflow-y: hidden;
}
.Playbackdetails-preview .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.Playbackdetails-preview .preview-pic {
  display: block;
  width: 100%;
  padding: 60px 0;
}
.Playbackdetails-preview .preview-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.Playbackdetails-preview .preview-pic .top {
  height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  padding: 20px 60px;
  text-align: center;
  width: 100%;
  display: block;
  z-index: 999;
  top: 0;
}
.Playbackdetails-preview .preview-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.Playbackdetails-preview .preview-pic .top .img img {
  display: block;
}
.Playbackdetails-preview .preview-pic .preview-box {
  margin: 40px 40px 0;
  display: block;
  position: relative;
}
.Playbackdetails-preview .preview-pic .preview-box .left {
  width: 100%;
  float: left;
  background: #111111;
  border-radius: 6.14698px;
  padding: 30px 20px;
  text-align: center;
  min-height: 300px;
  height: 100%;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box {
  margin: 25px 0 25px;
  padding: 0 30px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box .row-box {
  display: flex;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box .but-box {
  width: 140px;
  height: 28px;
  background: linear-gradient(90deg, #5B93FF 0%, #00BBF8 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5px;
  border: none;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box .but-box span {
  color: #fff;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box .img-icon {
  cursor: pointer;
  display: inline-block;
  margin: 0 5px;
  line-height: 36px;
}
.Playbackdetails-preview .preview-pic .preview-box .left .video-box .img-icon img {
  width: 28px;
}
.Playbackdetails-preview .preview-pic .preview-box .left .pic_img {
  display: block;
  width: 100%;
  overflow: auto;
  height: 560px;
}
.Playbackdetails-preview .preview-pic .preview-box .left embed {
  width: 100%;
  height: 100%;
}
.Playbackdetails-preview .preview-pic .preview-box .left .preview_img {
  max-width: 100%;
  display: inline-block;
}
.Playbackdetails-preview .preview-pic .preview-box .right {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 28%;
  float: right;
  background: #111111;
  border-radius: 6.14698px;
  padding: 25px 30px;
}
.Playbackdetails-preview .preview-pic .preview-box .right .span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
  display: block;
}
.Playbackdetails-preview .preview-pic .preview-box .right .text {
  padding: 30px 0 30px 105px;
  display: block;
  position: relative;
}
.Playbackdetails-preview .preview-pic .preview-box .right .text .img {
  position: absolute;
  left: 0;
  top: 50%;
  width: 89px;
  height: 89px;
  border-radius: 50%;
  transform: translate(0, -50%);
}
.Playbackdetails-preview .preview-pic .preview-box .right .text .img img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.Playbackdetails-preview .preview-pic .preview-box .right .text .h4 {
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.Playbackdetails-preview .preview-pic .preview-box .right .text .p {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #FFFFFF;
  opacity: 0.5;
  display: block;
  margin: 0 0 8px 0;
}
.Playbackdetails-preview .preview-pic .preview-box .right .text .p .spana {
  padding-right: 6px;
  display: inline-block;
}
.Playbackdetails-preview .preview-pic .preview-box .right .Coll {
  margin-top: 20px;
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: #FFFFFF;
  vertical-align: middle;
  height: 30px;
}
.Playbackdetails-preview .preview-pic .preview-box .right .Coll img {
  width: 16px;
  margin-right: 10px;
  margin-top: -6px;
  vertical-align: middle;
}
.Playbackdetails-preview .preview-pic .preview-box .right .Coll span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  opacity: 0.99;
  margin-left: 10px;
  padding-left: 25px;
  position: relative;
}
.Playbackdetails-preview .preview-pic .preview-box .right .Coll span .icon {
  width: 16px;
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.Playbackdetails-preview #img_content {
  overflow: hidden;
  height: auto;
}
html {
  overflow-y: auto;
  overflow-x: auto;
}
@media screen and (min-width: 1366px) and (max-width: 1800px) {
  .resources-preview .preview-pic .preview-box .left .pic_img {
    height: 580px;
  }
}
